import React, { Component} from 'react';

import axios from '../../../axios-orders';
import Button from '../../../components/UI/Button/Button';
import Spinner from '../../../components/UI/Spinner/Spinner';
import Input from '../../../components/UI/Input/Input';

import classes from './ContactData.css';

class InputHelper {

    constructor(elementTag, elementType, elementPlaceholder, elementValue, elementValidation) {

        this.htmlElement = elementTag;

        if(elementTag === "select") {

            this.htmlElementConfig = {
                options: elementPlaceholder
            };

        } else {

            this.htmlElementConfig = {
                type: elementType,
                placeholder: elementPlaceholder
            };
        }
        
        this.value = elementValue || '';
        this.validation = elementValidation;
        this.valid = false;
        this.touched = false;
    }    
}

class ContactData extends Component {

    state = {
        orderForm: {
            name: new InputHelper('input', 'text', 'Your Name', '', {required: true}),
            street: new InputHelper('input', 'text', 'Street', '', {required: true}),
            postalCode: new InputHelper('input', 'number', 'Postal Code', '', {required: true, minLength: 3, maxLength: 5}),
            country: new InputHelper('input', 'text', 'Country', '', {required: true}),
            email: new InputHelper('input', 'email', 'Your Email', '', {required: true}),
            deliveryMethod: new InputHelper('select', '', [{value: 'fastest', display: 'Fastest'},{value: 'cheapest', display: 'Cheapest'}], 'fastest', {})
        },
        loading: false,
        formValid: false
    }

    orderHandler = (event) => {

        event.preventDefault();

        this.setState({loading: true});

        const formData = {};
        for(let key in this.state.orderForm) {
            formData[key] = this.state.orderForm[key].value;
        }
        
        const order = {
            ingredients: this.props.ingredients,
            price: this.props.totalPrice,
            orderData: formData

        }
        axios.post('/orders.json', order)
            .then(response => {
                console.log(response);
                this.setState({loading: false});
                this.props.history.push('/burgerbuilder');
            })
            .catch(error => {
                console.log(error);
                this.setState({loading: false});
            });
    }

    checkValidity(value, rules) {

        let isValid = true;

        if(rules.required) {
            isValid = value.trim() !== '' && isValid
        }

        if(rules.minLength) {
            isValid = value.length >= rules.minLength && isValid
        }

        if(rules.maxLength) {
            isValid = value.length <= rules.maxLength && isValid
        }
        
        return isValid;
    }

    inputChangeHandler = (event, inputIdentifier) => {

        const updatedFormData = {...this.state.orderForm};
        const updatedElemData = {...updatedFormData[inputIdentifier]};

        updatedElemData.value = event.target.value;
        updatedElemData.valid = this.checkValidity(updatedElemData.value, updatedElemData.validation)
        updatedElemData.touched = true;

        updatedFormData[inputIdentifier] = updatedElemData;

        let IsFormValid = true;
        for(let key in updatedFormData) {
            IsFormValid = updatedFormData[key].valid && IsFormValid;
        }
        
        this.setState({orderForm: updatedFormData, formValid: IsFormValid})
    }

    render() {

        let formElementsArr = [];

        for(let key in this.state.orderForm) {

            formElementsArr.push({
                id: key,
                config: this.state.orderForm[key]
            });
        }
        
        let form = (<form onSubmit={this.orderHandler}>
            {
                formElementsArr.map(formElem => (
                    <Input 
                    key={formElem.id}
                    htmlElement={formElem.config.htmlElement} 
                    htmlElementConfig={formElem.config.htmlElementConfig} 
                    value={formElem.config.value}
                    invalid={!formElem.config.valid}
                    shouldValidate={formElem.config.validation}
                    touched={formElem.config.touched}
                    changed={(event) => this.inputChangeHandler(event, formElem.id)} />
                ))
            }
            <Button btnType="Success" disabled={!this.state.formValid}>ORDER</Button>
        </form>);

        if(this.state.loading) {
            form = <Spinner />
        }

        return (
            <div className={classes.ContactData}>
                <h4>Enter your Contact Data</h4>
                {form}
            </div>
        )
    }

}

export default ContactData;


