import React from 'react';

import classes from './Order.css'

const order = (props) => {

    const ingredients = [];
    for (let igKey in props.ingredients) {
        ingredients.push({
            name: igKey,
            amount: props.ingredients[igKey]
        })
    }

    const igOutput = ingredients.map(ig => {
        return <span key={ig.name}>{ig.name} ({ig.amount})</span>
    })

    return (
        <div className={classes.Order}>
            <p>Ingredients: {igOutput}</p>
            <p>Price: USD {props.price}</p>
        </div>
    )
}

export default order;


